// Defaults Partial
/* global box-sizing */
*,
*:after,
*:before {
	-moz-box-sizing:border-box;
	box-sizing:border-box;
	-webkit-font-smoothing:antialiased;
	font-smoothing:antialiased;
	text-rendering:optimizeLegibility;
}
h1, h2, h3, h4, h5, h6 { 
    margin: ($spacing * 1.5) 0 ($spacing * 0.5) 0
}
h1, .h1 {
    font-size: 1.8em;
}
h2, .h2 {
    font-size: 1.6em;
}
h3, .h3 {
    font-size: 1.4em;
}
h4, .h4 {
    font-size: 1.2em;
}
h5, .h5 {
    font-size: 1em;
}
h6, .h6 {
    font-size: 0.8em;
}
p {
    margin: ($spacing * 0.5) 0;
}
.clear {
    *zoom:1;
    &:before, &:after {
        content:' ';
        display:table;
    }
    &:after {
        clear:both;
    }
}
img {
    max-width:100%;
    vertical-align:bottom;
}
a {
    color: $text-color;
    text-decoration: none;
    border-bottom: 3px dotted $grey-base;
    transition: all .2s ease-out;
    &:hover,
    &:focus {
        background: $grey-light;
        border-bottom-color: $brand;
    }

    &:focus,
    &:hover,
    &:active {
        outline:0;
    }
}
legend {
    
}
$w: 2px;
input {
    border: $w dotted transparent;
    border-bottom-color: $grey-base;
}
textarea, fieldset {
    border: $w dotted $grey-base;
}
input, textarea {
    font-family: $ss-family;
    padding: .25rem;
    &:focus {
        outline:0;
        border: $w solid $brand;
    }
}
input {
    &[type="search"] {
        box-sizing: border-box;
        border-radius: 0 !important;
    }

    &[type="checkbox"] {
        position: relative;
        height: 0.8rem;
        width: 0.8rem;
        &:focus {
            outline: 2px solid $brand;
        }
        &:checked:focus {
            outline: 2px solid $grey-base;
        }
    }
}

.hidden {
    display: none;
}
.visually-hidden { 
  position: absolute; 
  overflow: hidden; 
  clip: rect(0 0 0 0); 
  height: 1px; width: 1px; 
  margin: -1px; padding: 0; border: 0; 
}
.sr-only {
    display: block;
    overflow: hidden !important;
    width: 0 !important;
    height: 0 !important;
}


// icons
.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    transition: fill .2s ease-in-out;
    fill: currentColor;
}
.icon--full-block {
    display: block;
    width: auto;
    height: 100%;
    max-width: 100%;
}


